.card {
  background: #fff;
  border-radius: 5px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 90%;
  overflow: hidden;
  background: black;
  background-size: contain;
}
.card > .img-container > img {
  width: 100%
}

.card > .content {
  /* padding-left: 1rem;
  padding-right: 1rem; */
  /* font-size: 15px; */
  align-content: center;

}

.card > .content > p {
  /* margin-top: 18px; */
  text-align: center;
  align-content: center;
  font-family: Cambria;
  font-size: 16px;
}

.card > .shuffle {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wrapper {
  padding-top: 50px;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  background-image: url(/img/mayBack.jpg);
  background-size: cover;
}
.header {
    width: 100%;
    font-size: 120px;
    font-family: Cambria;
    background-color: black;
    color: gold;
    text-align: center;
}
p {
    text-align: left;
    font-size: 48px;
    font-family: Cambria;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

